export default function Cart(){
    return(
        <div className="cart">
            <div className="inner">
                <div className="cartHeader">
                    <p></p>
                </div>
                <div className="cartFooter">
                    <button className="btnBuy btn"> </button>
                    <button className="btnClose btn" onClick={()=>{
                        document.querySelector('.cart').classList.remove('active')
                    }}> </button>
                </div>
            </div>
        </div>
    )
}